/* Estilos generales */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-image: url('../imagen/perfil.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.profile-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background-color: #a09e9ed6;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.profile-container h2 {
    color: #333;
    margin-bottom: 20px;
}

#profileInfo {
    text-align: left;
}

#profileInfo p {
    margin-bottom: 10px;
}

#profileInfo strong {
    font-weight: bold;
    margin-right: 5px;
    color: #333;
}


